:root {
    --prim: #4159E0;
    --text: #222;
    --borderRad: 40px;
    --boxShad: 0 4px 4px #22222260;
    --background: #f9f9f9;
} 

*,*::before,*::after {
    margin: 0;
    color: var(--text);
}

html {
    box-sizing: border-box;
    font-family: Radio Canada, sans-serif;
}
body {
    margin: 0;;
    background-color: var(--background);
}

header {
    background-color: var(--prim);
    display: flex;
    justify-content: center;
    height: 5rem;
    align-items: center;
    font-size: 1.2rem;
    box-shadow: var(--boxShad);
}
h1 {
    color: var(--background);
}
h2 {
    text-align: center;
}
main {
    display: flex;
    flex-direction: column;
    margin: 2rem auto;
}
form {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 3rem auto;
    width: 90%;
    justify-content: center;
}
form div {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    padding: 0.6rem;
    justify-content: space-between;
    width: 90%;
    gap: 1em;
    margin-bottom: 1rem;
    align-items: center;
}
fieldset {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    border: none;
    margin-top: 1rem;
    padding: 0;
}
legend {
    font-weight: 800;
    position: relative;
    left: 2rem;
}

label {
    border: 3px solid #4159E0;
    border-radius: var(--borderRad);
    background-color: #FFF;
    box-shadow: var(--boxShad);
    display: flex;
    justify-content: center;
    padding: 0.5em 0;
    width: 80%;
    font-weight: 600;

}
select {
    background: url('https://api.iconify.design/bx/chevron-down.svg?height=28') 96% / 15% no-repeat;
    padding: 5px 35px 5px 5px;
    height: 100%;
}
select, input {
    border: solid 3px #4159E0;
    background-color: #FFF;
    border-radius: var(--borderRad);
    box-shadow: var(--boxShad);
    text-align: center;
    font-size: 18px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 80%; 
}
input {
    height: 2rem;
}

button {
    background-color: var(--prim);
    color: var(--background);
    padding: 1rem;
    font-size: 1.5rem;
    border-radius: var(--borderRad);
    font-weight: bold;
    width: 55%;
    cursor: pointer;
    margin-top: 1rem;
}
#locationErr, #typeErr, #methodErr, #offerErr {
    display: none;
    color: red;
    font-weight: bold;
    font-size: 0.8rem;
    margin: -1rem auto 0;
}
option:disabled {
    display: none;
}
label> abbr.optional {
    color: #888;
    font-weight: normal;
    text-decoration: none;
    cursor: pointer;
}
label>abbr.required, legend>abbr.required {
    color: red;
    text-decoration: none;
} 
.hidden {
    display: none !important;
    transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.8s;
}
.results {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
}
.results button {
    margin: 1rem auto 2rem;
}
.results .result {
    margin: 2rem auto;
    position: relative;
    width: 75%;
    box-shadow: 0 4px 4px 0 #00000082;
    border-radius: 1rem;
    padding: 1rem;
    background-color: #FFF;
}
.results h2 {
    text-align: left;
    margin-bottom: 1rem;
}
.tags {
    display: flex;
    gap: 3px;
    position: relative;
    top:-0.75rem
}
.tag {
    padding: 5px 10px;
    border-radius: var(--borderRad);
    color: var(--background);
}
.offerTag {
    border: var(--prim) 2px solid;
    color: var(--prim);
    font-weight: bold;
}
.netTag {
    border: #db0000 2px solid;
    color: #db0000;
    font-weight: bold;
  }
  

.rating {
    margin-top: 1rem;
}
  
h3 {
    position: relative;
    top: -0.5rem;
    color: #222222ad;
}
  
h3::before {
    content: "Â°";
    color: dimgray;
}

.tagLegende {
    display: flex;
    align-items: center;
}
.tagLegende ul {
    display: flex;
    list-style: none;
    align-items: center;
    gap: 1rem;
}
.tagLegende h2 {
  margin-bottom: 0;
}

  

@media (min-width: 500px){
    form {
        width: min(80%,28rem);
        margin: 1rem auto;
    }
    form div {
        flex-direction: row;
        width: 100%;
    }
    select, input {
        width: 60%;
        margin: 0 1rem;
    }
    select {
        height: 100%;
    }
    label {
        padding: 1em;
        width: 25%;
    }
    input {
        height: 90%;
    }
    .tags {
        position: absolute;
        right: 0.5rem;
        top: 1rem;
    }
    .results h2 {
        max-width: 80%;
    }
    
}